#groups-checkbox-tree {
  ol {
    padding-inline-start: 24px;
    list-style: none;
  }

  li {
    user-select: none;
  }

  input[type="checkbox"] {
    display: none;
  }

  button.rct-collapse {
    margin-left: -24px;
    padding: 0;
    background-color: transparent;
    border: none;
  }

  .rct-checkbox {
    color: #007bff;
  }

  .rct-checkbox,
  .rct-title {
    cursor: pointer;
  }
}
