html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  overflow: hidden;
}

#name_display {
  text-transform: capitalize;
}

.schedule {
  flex-grow: 1;
  overflow: auto;
}

.help {
  cursor: help;
}

.vis-inner {
  width: 100%;
}

.vis-dot,
.vis-line {
  display: none;
}

.vtuber {
  text-align: center;
}

.vtuber img {
  width: 5em;
  border-radius: 2.5em;
}

.stream img {
  height: 6em;
}

.vis-item:hover {
  z-index: 10000 !important;
}

.navbar {
  .navbar-brand {
    font-weight: bold;
  }
  .navbar-nav {
    .nav-link:last-child {
      padding-right: 0;
    }
  }
}

.notification-toast {
  h1 {
    font-size: 1.2em;
  }
  img {
    display: block;
    height: 4em;
    margin-right: 1em;
    float: left;
  }
  p {
    margin-bottom: 0;
  }
}

.Toastify__toast-container {
  width: 480px;
}

.ga-consent {
  position: fixed;
  bottom: 0;
  width: 100%;
  padding: 1em;
  text-align: center;
  background-color: #eee;

  .btn {
    margin: 0 0.5em;
  }
}

.filters {
  .dropdown-toggle.btn {
    border-radius: 0;
  }

  .dropdown-header,
  .dropdown-item {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
  }

  .icon-button {
    user-select: none;
  }

  .search {
    cursor: help;
  }

  .scroll {
    cursor: pointer;
  }

  .save {
    cursor: pointer;
  }

  .clear-search {
    cursor: pointer;

    .material-icons {
      border-radius: 0.5em;
      font-size: 1.2rem;

      &:hover {
        background-color: rgba(0, 0, 0, 0.1);
      }
    }
  }
}

.about-modal,
.search-help-modal {
  .material-icons {
    vertical-align: bottom;
  }
}
